@import "../../../../variables";

.switchable-bar {
  // match action bar
  min-width: 370px;
  min-height: 120px;
  position: relative;
  border: 1px solid grey;
  border-top: none;
  // background: rgba(255, 255, 255, 0.25);
  pointer-events: initial;
  display: flex;
  justify-content: center;
  align-items: center;

  .switcher {
    position: absolute;
    top: 60px;
    transform: translateY(-50%);
    margin: 0 2px;

    &.right {
      left: 100%;
    }

    &.left {
      right: 100%;
    }
  }
}

.double-bar {
  display: flex;
  // flex-direction: column;

  // > :not(.active) {
  //   transform: scale(0.5);
  // }
}

.tool-bar {
  display: flex;
  flex-direction: row;

  .tool {
    padding: 10px;
  }

  .tool.selected {
    transform: scale(1.25);
  }

  .interact-tool {
    background-image: url("../../../../assets/images/tether-icon.png");
    cursor: pointer;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
  }
}
